<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col-md-12">
                    <div class="element-wrapper">
                        <h2 class="element-header">
                            Programas
                        </h2>
                        <div class="control-header">
                            <div class="row align-items-center">
                                <div class="col-8 col-lg-7">
                                    <div class="form-group mr-4">
                                        <label class="mr-2" for="">Mostar por:</label>
                                        <select class="form-control-sm" @change="changeSelect">
                                            <option value="1">
                                                Todos
                                            </option>
                                            <option value="2">
                                                Ativos
                                            </option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
            <card-list></card-list>
        </div>
    </div>
</template>
<script>

    import Info from '../../../components/ElementInfo';
    import CardList from './CardList';
    import programsMixis from '../../../mixins/programs';

    export default {
        mixins: [programsMixis],
        components: {
            Info,
            CardList
        },
        methods: {
            changeSelect(e) {
                switch (e.target.value) {
                    case '1':
                        this.preload(true);
                        this.getPrograms().then(() => this.preload(false));
                        break;

                    case '2':
                        this.preload(true);
                        this.getActivePrograms().then(() => this.preload(false));
                        break;
                    default:
                        break;
                }
            }
        }
    }
</script>

